import Bienvenida from "./sound/Bienvenida.ogg";
import BRAZOS1 from "./sound/BRAZOS1.ogg";
import BRAZOS2 from "./sound/BRAZOS2.ogg";
import BRAZOS3 from "./sound/BRAZOS3.ogg";
import BRAZOS4 from "./sound/BRAZOS4.ogg";
import CUELLO1 from "./sound/CUELLO1.ogg";
import CUELLO2 from "./sound/CUELLO2.ogg";
import CUELLO3 from "./sound/CUELLO3.ogg";
import ESPALDA1 from "./sound/ESPALDA1.ogg";
import ESPALDA2 from "./sound/ESPALDA2.ogg";
import GINMASIA_CEREBRAL from "./sound/GINMASIA_CEREBRAL.ogg";
import HOMBROS1 from "./sound/HOMBROS1.ogg";
import HOMBROS2 from "./sound/HOMBROS2.ogg";
import MANOS1 from "./sound/MANOS1.ogg";
import MANOS2 from "./sound/MANOS2.ogg";
import MANOS3 from "./sound/MANOS3.ogg";
import OJOS1 from "./sound/OJOS1.ogg";
import OJOS2 from "./sound/OJOS2.ogg";
import OJOS3 from "./sound/OJOS3.ogg";
import PIERNAYCADERA1 from "./sound/PIERNAYCADERA1.ogg";
import PIERNAYCADERA2 from "./sound/PIERNAYCADERA2.ogg";
import PIERNAYCADERA3 from "./sound/PIERNAYCADERA3.ogg";
import PIERNAYCADERA4 from "./sound/PIERNAYCADERA4.ogg";
import PIERNAYCADERA6 from "./sound/PIERNAYCADERA6.ogg";
import ting from "./sound/ting.mp3";

import useSound from "use-sound";
import { useEffect, useState } from "react";

export const LoadSound = () => {
  const [BienvenidaPlay, BienvenidaExpo] = useSound(Bienvenida);
  const [BRAZOS1Play, BRAZOS1Expo] = useSound(BRAZOS1);
  const [BRAZOS2Play, BRAZOS2Expo] = useSound(BRAZOS2);
  const [BRAZOS3Play, BRAZOS3Expo] = useSound(BRAZOS3);
  const [BRAZOS4Play, BRAZOS4Expo] = useSound(BRAZOS4);
  const [CUELLO1Play, CUELLO1Expo] = useSound(CUELLO1);
  const [CUELLO2Play, CUELLO2Expo] = useSound(CUELLO2);
  const [CUELLO3Play, CUELLO3Expo] = useSound(CUELLO3);
  const [ESPALDA1Play, ESPALDA1Expo] = useSound(ESPALDA1);
  const [ESPALDA2Play, ESPALDA2Expo] = useSound(ESPALDA2);
  const [GINMASIA_CEREBRALPlay, GINMASIA_CEREBRALExpo] =
    useSound(GINMASIA_CEREBRAL);
  const [HOMBROS1Play, HOMBROS1Expo] = useSound(HOMBROS1);
  const [HOMBROS2Play, HOMBROS2Expo] = useSound(HOMBROS2);
  const [MANOS1Play, MANOS1Expo] = useSound(MANOS1);
  const [MANOS2Play, MANOS2Expo] = useSound(MANOS2);
  const [MANOS3Play, MANOS3Expo] = useSound(MANOS3);
  const [OJOS1Play, OJOS1Expo] = useSound(OJOS1);
  const [OJOS2Play, OJOS2Expo] = useSound(OJOS2);
  const [OJOS3Play, OJOS3Expo] = useSound(OJOS3);
  const [PIERNAYCADERA1Play, PIERNAYCADERA1Expo] = useSound(PIERNAYCADERA1);
  const [PIERNAYCADERA2Play, PIERNAYCADERA2Expo] = useSound(PIERNAYCADERA2);
  const [PIERNAYCADERA3Play, PIERNAYCADERA3Expo] = useSound(PIERNAYCADERA3);
  const [PIERNAYCADERA4Play, PIERNAYCADERA4Expo] = useSound(PIERNAYCADERA4);
  const [PIERNAYCADERA6Play, PIERNAYCADERA6Expo] = useSound(PIERNAYCADERA6);
  const [tingPlay, tingExpo] = useSound(ting);

  const [loaded, setLoaded] = useState(false);

  useEffect(() => {
    if (
      BienvenidaExpo?.duration &&
      BRAZOS1Expo?.duration &&
      BRAZOS2Expo?.duration &&
      BRAZOS3Expo?.duration &&
      BRAZOS4Expo?.duration &&
      CUELLO1Expo?.duration &&
      CUELLO2Expo?.duration &&
      CUELLO3Expo?.duration &&
      ESPALDA1Expo?.duration &&
      ESPALDA2Expo?.duration &&
      GINMASIA_CEREBRALExpo?.duration &&
      HOMBROS1Expo?.duration &&
      HOMBROS2Expo?.duration &&
      MANOS1Expo?.duration &&
      MANOS2Expo?.duration &&
      MANOS3Expo?.duration &&
      OJOS1Expo?.duration &&
      OJOS2Expo?.duration &&
      OJOS3Expo?.duration &&
      PIERNAYCADERA1Expo?.duration &&
      PIERNAYCADERA2Expo?.duration &&
      PIERNAYCADERA3Expo?.duration &&
      PIERNAYCADERA4Expo?.duration &&
      PIERNAYCADERA6Expo?.duration &&
      tingExpo
    ) {
      setLoaded(true);
    }
  }, [
    BienvenidaExpo,
    BRAZOS1Expo,
    BRAZOS2Expo,
    BRAZOS3Expo,
    BRAZOS4Expo,
    CUELLO1Expo,
    CUELLO2Expo,
    CUELLO3Expo,
    ESPALDA1Expo,
    ESPALDA2Expo,
    GINMASIA_CEREBRALExpo,
    HOMBROS1Expo,
    HOMBROS2Expo,
    MANOS1Expo,
    MANOS2Expo,
    MANOS3Expo,
    OJOS1Expo,
    OJOS2Expo,
    OJOS3Expo,
    PIERNAYCADERA1Expo,
    PIERNAYCADERA2Expo,
    PIERNAYCADERA3Expo,
    PIERNAYCADERA4Expo,
    PIERNAYCADERA6Expo,
    tingExpo,
  ]);

  return {
    sounds: {
      Bienvenida: { BienvenidaPlay, BienvenidaExpo },
      BRAZOS1: { BRAZOS1Play, BRAZOS1Expo },
      BRAZOS2: { BRAZOS2Play, BRAZOS2Expo },
      BRAZOS3: { BRAZOS3Play, BRAZOS3Expo },
      BRAZOS4: { BRAZOS4Play, BRAZOS4Expo },
      CUELLO1: { CUELLO1Play, CUELLO1Expo },
      CUELLO2: { CUELLO2Play, CUELLO2Expo },
      CUELLO3: { CUELLO3Play, CUELLO3Expo },
      ESPALDA1: { ESPALDA1Play, ESPALDA1Expo },
      ESPALDA2: { ESPALDA2Play, ESPALDA2Expo },
      GINMASIA_CEREBRAL: { GINMASIA_CEREBRALPlay, GINMASIA_CEREBRALExpo },
      HOMBROS1: { HOMBROS1Play, HOMBROS1Expo },
      HOMBROS2: { HOMBROS2Play, HOMBROS2Expo },
      MANOS1: { MANOS1Play, MANOS1Expo },
      MANOS2: { MANOS2Play, MANOS2Expo },
      MANOS3: { MANOS3Play, MANOS3Expo },
      OJOS1: { OJOS1Play, OJOS1Expo },
      OJOS2: { OJOS2Play, OJOS2Expo },
      OJOS3: { OJOS3Play, OJOS3Expo },
      PIERNAYCADERA1: { PIERNAYCADERA1Play, PIERNAYCADERA1Expo },
      PIERNAYCADERA2: { PIERNAYCADERA2Play, PIERNAYCADERA2Expo },
      PIERNAYCADERA3: { PIERNAYCADERA3Play, PIERNAYCADERA3Expo },
      PIERNAYCADERA4: { PIERNAYCADERA4Play, PIERNAYCADERA4Expo },
      PIERNAYCADERA6: { PIERNAYCADERA6Play, PIERNAYCADERA6Expo },
      ting: { tingPlay, tingExpo },
    },
    loaded,
  };
};
