import React, { useEffect, useState } from "react";
import { makeStyles } from "@material-ui/core";
import { LoadSound } from "./LoadSoung";
import { config, rutinas, dates } from "./config";
import images from "./images";
import { CountdownCircleTimer } from "react-countdown-circle-timer";
import "./timer.css";

const useStyles = makeStyles({
  card: {
    maxWidth: "90vw",
    minWidth: "80vw",
    textAlign: "center",
  },
  image: {
    maxWidth: "80%",
  },
  init: {
    display: "flex",
    justifyContent: "center",
    alignItems: "center",
    borderRadius: "50%",
    background: "#1abc9c",
    height: "calc(90vw - 80px)",
    width: "calc(90vw - 80px)",
    fontSize: "2em",
    textTransform: "uppercase",
    color: "white",
    boxShadow: "0 10px 20px rgba(0,0,0,.2)",
    cursor: "pointer",
    userSelect: "none",
  },
  routine: {
    display: "flex",
    flexDirection: "column",
    justifyContent: "center",
    alignItems: "center",
    minHeight: "90vh",
    minWidth: "90vw",
  },
  timer: {
    fontFamily: "Montserrat",
    display: "flex",
    flexDirection: "column",
    alignItems: "center",
  },

  text: {
    color: "#aaa",
  },

  value: {
    fontSize: "40px",
  },
});

function fmtMSS(s) {
  return (s - (s %= 60)) / 60 + (9 < s ? ":" : ":0") + s;
}

const renderTime = ({ remainingTime }) => {
  if (remainingTime === 0) {
    return <div className="timer">Espere por favor</div>;
  }

  return (
    <div className="timer">
      <div className="value">{fmtMSS(remainingTime)}</div>
    </div>
  );
};

export const Home = () => {
  const date = new Date();
  const day = date.getDay();
  const classes = useStyles();
  const [currentFrame, setCurrentFrame] = useState();
  const [init, setInit] = useState(false);
  const [currentRutina, setCurrentRutina] = useState("");
  const [start, setStart] = useState(true);
  const { sounds, loaded } = LoadSound();
  const [timer, setTimer] = useState(false);
  const [img, setImage] = useState("");
  const [noRutina, setNoRutina] = useState("false");

  const exec = async () => {
    const rotuine = config[currentRutina];
    if (rotuine) {
      const actions = rotuine.a;
      for (let action of actions) {
        setCurrentFrame(action);
        const audioName = action.audio;
        if (action.image) setImage(action.image);
        try {
          if (audioName) {
            sounds[audioName][`${audioName}Play`]();
            if (action.timer) {
              await new Promise((resolve, reject) =>
                setTimeout(
                  resolve,
                  sounds[audioName][`${audioName}Expo`].duration
                )
              );
              setTimer(true);
              await new Promise((resolve, reject) =>
                setTimeout(resolve, action.time)
              );
              setImage("");
              sounds.ting[`tingPlay`]();
              await new Promise((resolve, reject) =>
                setTimeout(resolve, sounds.ting[`tingExpo`].duration)
              );
              setTimer(false);
              setCurrentFrame();
            } else {
              await new Promise((resolve, reject) =>
                setTimeout(
                  resolve,
                  sounds[audioName][`${audioName}Expo`].duration + 1000
                )
              );
            }
          }
        } catch (error) {}
        setImage("");
        setCurrentFrame();
      }
      setImage("");
      setCurrentFrame();
      setInit(false);
      if (start) {
        setNoRutina(!!config[rutinas[dates[day]]]);
        setCurrentRutina(rutinas[dates[day]]);
        setStart(false);
      }
    }
  };

  useEffect(() => {
    setInit(false);
  }, []);

  useEffect(() => {
    if (loaded) {
      setCurrentRutina(rutinas.bienvenida);
      setInit(true);
    }
  }, [loaded]);

  useEffect(() => {
    if (init && currentRutina) {
      exec(currentRutina);
    }
  }, [init, currentRutina]);

  return (
    <div>
      {loaded && !init && noRutina && (
        <div className={classes.routine} onClick={() => setInit(true)}>
          <div className={classes.init}>{currentRutina}</div>
        </div>
      )}
      {loaded && !init && !noRutina && (
        <div className={classes.routine}>
          Sin rutinas en el momento {noRutina}
        </div>
      )}
      {loaded && init && currentFrame && (
        <div className={classes.routine}>
          <div style={{ textAlign: "center" }}>
            {img && (
              <img src={images[`img_${img}`]} className={classes.image} />
            )}
          </div>
          {timer && (
            <CountdownCircleTimer
              onComplete={() => {
                return [false]; // repeat animation in 1.5 seconds
              }}
              isPlaying
              duration={currentFrame.time / 1000}
              colors={[["#2980b9", 0.33], ["#8e44ad", 0.33], ["#27ae60"]]}
            >
              {renderTime}
            </CountdownCircleTimer>
          )}
        </div>
      )}
    </div>
  );
};
