import React, { useContext, useState } from "react";
import {
  Button,
  CircularProgress,
  Grid,
  makeStyles,
  Snackbar,
  TextField,
} from "@material-ui/core";
import Alert from "@material-ui/lab/Alert";

import firebase from "./firebase.config";
import { blue, red } from "@material-ui/core/colors";
import { AuthContext } from "./AuthContext";

const useStyles = makeStyles((theme) => ({
  contentProgress: {
    position: "absolute",
    top: "0",
    left: "0",
    background: blue[300],
    width: "100%",
    height: "100%",
    opacity: "0.6",
    borderRadius: "10px",
  },
  progress: {
    color: blue[500],
    position: "absolute",
    top: "50%",
    left: "50%",
    marginTop: -12,
    marginLeft: -12,
  },
}));

const validations = {
  email(value) {
    return /^[\w-\.]+@([\w-]+\.)+[\w-]{2,4}$/g.test(value)
      ? false
      : "No parece un email";
  },
  password(value) {
    return value && value.length >= 6
      ? false
      : "Se requiere una contrase√±a de m√≠nimo 6 caracteres";
  },
};

const useForm = () => {
  const { login } = useContext(AuthContext);
  const [fields, setFields] = useState({
    email: "",
    password: "",
  });

  const [errors, setErrors] = useState({
    email: false,
    password: false,
  });

  const [error, setError] = useState("");

  const [loading, setLoading] = useState(false);

  const onCloseError = () => setError("");
  const onChange = (e) => {
    if (e && e.target) {
      const name = e.target.name;
      const value = e.target.value;
      setFields({
        ...fields,
        [name]: value,
      });
      setErrors({
        ...errors,
        [name]: validations[name](value),
      });
    }
  };

  const onRegister = async () => {
    if (
      Object.values(fields).some((e) => e) &&
      !Object.values(errors).some((e) => e)
    ) {
      setLoading(true);
      try {
        await firebase
          .auth()
          .createUserWithEmailAndPassword(fields.email, fields.password);
        await firebase.database().ref("user").set({
          name: "",
          email: fields.email,
        });
        await onLogin();
      } catch (error) {
        if (error?.code === "auth/email-already-in-use")
          setError("El email ya se encuentra uso");
        else
          setError(
            error && error.message
              ? error.message
              : "Ocurrio un error inesperado"
          );
      }
      setLoading(false);
    } else setError("Debe diligenciar el formulario");
  };

  const onLogin = async () => {
    if (
      Object.values(fields).some((e) => e) &&
      !Object.values(errors).some((e) => e)
    ) {
      setLoading(true);
      try {
        const { user } = await firebase
          .auth()
          .signInWithEmailAndPassword(fields.email, fields.password);
        login({
          email: user.email,
          uid: user.uid,
        });
        console.log(user);
      } catch (error) {
        if (error?.code === "auth/user-not-found")
          setError("El usuario no se encuentra registrado");
        else
          setError(
            error && error.message
              ? error.message
              : "Ocurrio un error inesperado"
          );
      }
      setLoading(false);
    } else setError("Debe diligenciar el formulario");
  };

  return {
    onLogin,
    fields,
    onChange,
    onRegister,
    errors,
    error,
    onCloseError,
    loading,
  };
};

export const Login = () => {
  const classes = useStyles();
  const {
    onLogin,
    fields,
    onChange,
    onRegister,
    errors,
    error,
    onCloseError,
    loading,
  } = useForm();
  return (
    <Grid container>
      <Snackbar
        anchorOrigin={{ vertical: "top", horizontal: "center" }}
        open={!!error}
        autoHideDuration={6000}
        onClose={onCloseError}
      >
        <Alert onClose={onCloseError} severity="error">
          {error}
        </Alert>
      </Snackbar>
      <Grid item style={{ position: "relative", padding: "10px" }}>
        <Grid container direction="column" spacing={4}>
          <Grid item>
            <TextField
              fullWidth
              helperText={errors["email"]}
              error={errors["email"]}
              name="email"
              value={fields.email}
              type="email"
              onChange={onChange}
              label="Email"
            />
          </Grid>
          <Grid item>
            <TextField
              fullWidth
              name="password"
              helperText={errors["password"]}
              error={errors["password"]}
              value={fields.password}
              type="password"
              onChange={onChange}
              label="Contrase√±a"
            />
          </Grid>
          <Grid item>
            <Grid container spacing={4}>
              <Grid item>
                <Button variant="contained" onClick={onLogin}>
                  Iniciar sesi√≥n
                </Button>
              </Grid>
              <Grid item>
                <Button
                  variant="contained"
                  color="primary"
                  onClick={onRegister}
                >
                  Registrate
                </Button>
              </Grid>
            </Grid>
          </Grid>
        </Grid>
        {loading && (
          <div className={classes.contentProgress}>
            {loading && (
              <CircularProgress size={24} className={classes.progress} />
            )}
          </div>
        )}
      </Grid>
    </Grid>
  );
};
